import React from 'react';
import './App.css';

function App() {
    return (
        <div className="App">
            <Title/>
            <Hellos/>
        </div>
    );
}

export default App;

class Title extends React.Component {
    render() {
        return (<p>This is a title</p>)
    }
}

class Hellos extends React.Component{
    constructor(props) {
        super(props);
        this.state={count:3};
        this.handleChange=this.handleChange.bind(this);
    }

    handleChange(event){
        let count=0;
        if(!isNaN(parseInt(event.target.value))){
            count=parseInt(event.target.value)
        }
        this.setState({count:count});
    }

    render() {
        // let oneSent=(<p>hello</p>);
        let outs=[];
        const count=this.state.count;
        outs[0]=(<input type="text" value={this.state.count} onChange={this.handleChange} key={"0"}/>)
        for (let i = 1; i <1+count; i++) {
            outs[i]=(<p key={i.toString()}>hello</p>)
        }
        return(outs)
    }
}